body,ul,li{
  margin: 0;
  padding: 0;
}
html,body{
  height: 100%;
}
body{
  font: 12px/1.5 tahoma;
}
li{
  list-style-type: none;
}
a:link, a:visited{
  text-decoration: none;
}
a:hover{
  text-decoration: underline;
}
#outer{
  width: 500px;
  margin: 0 auto;
  overflow: hidden;
  zoom: 1;
}
#skin, #nav{
  overflow: hidden;
  zoom: 1;
}
#skin{
  margin: 10px 0;
}
#skin li{
  float: left;
  width: 6px;
  height: 6px;
  cursor: pointer;
  overflow: hidden;
  margin-right: 10px;
  text-indent: -9999px;
  border-width: 4px;
  border-style: solid;
}
#skin li.current{
  background: #fff !important;
}
#red{
  border-color: red;
  background: red;
}
#green{
  border-color: green;
  background: green;
}
#black{
  border-color: black;
  background: black;
}
#nav{
  border: 1px solid #fff;
}
#nav li{
  float: left;
  width: 82px;
  line-height: 25px;
  text-align: center;
  border-right: 1px solid #fff;
}
#nav li.last{
  width: 83px;
  border-right-width: 0cm;
}
#nav li a{
  color: #fff;
}
#nav.red{
  background: red;
  border-color: #fff;
}
#nav.red li{
  border-color: #fff;
}
#nav.green{
  background: green;
  border-color: #fff;
}
#nav.green li{
  border-color: #fff;
}
#nav.black{
  background: black;
}